import React from 'react';

const Rating = ({ rating, numReviews, caption }) => {
    return (
        <div className='rating'>
            <span>
                <i className={rating >= 1 ? 'fas fa-star' : rating >= .5 ? 'fas fa-star-half-alt' : 'far fa-start'} />
            </span>
            <span>
                <i className={rating >= 2 ? 'fas fa-star' : rating >= 1.5 ? 'fas fa-star-half-alt' : 'far fa-start'} />
            </span>
            <span>
                <i className={rating >= 3 ? 'fas fa-star' : rating >= 2.5 ? 'fas fa-star-half-alt' : 'far fa-start'} />
            </span>
            <span>
                <i className={rating >= 4 ? 'fas fa-star' : rating >= 3.5 ? 'fas fa-star-half-alt' : 'far fa-start'} />
            </span>
            <span>
                <i className={rating >= 5 ? 'fas fa-star' : rating >= 4.5 ? 'fas fa-star-half-alt' : 'far fa-start'} />
            </span>
            {
                caption ? (
                    <span>{caption}</span>) : (
                    <span>{' ' + numReviews + ' reviews'}</span>
                )
            }
        </div>
    );
};

export default Rating;
